.vc-form .radio-box,
.vc-form .check-box {
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    -webkit-align-items: center;
    align-items: center;
    cursor: pointer;
    padding: 18px;
}

.vc-form .form-box .radio,
.vc-form .form-box .check {
    display: none;
}

.vc-form .form-box .switch {
    border: 1px solid #ccc;
    border-radius: 50%;
    box-sizing: border-box;
    margin: 0 15px 0 0;
    height: 18px;
    position: relative;
    width: 18px;
}

.vc-form .form-box .radio:checked ~ .switch::after {
    border: 7px solid #1990c6;
    border-radius: 50%;
    box-sizing: border-box;
    bottom: -1px;
    content: ' ';
    left: -1px;
    position: absolute;
    right: -1px;
    top: -1px;
}

.vc-form .check-box .switch {
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    border-radius: 3px;
}

.vc-form .form-box .check:not(:checked) ~ .switch {
    background-color: white;
}

.vc-form .form-box .check:checked ~ .switch {
    background: #1990c6;
    border: 1px solid #1990c6;
}

.vc-form .form-box .check:checked ~ .switch::after {
    color: #fff;
    content: '';
    font-family: 'FontAwesome';
    font-size: 10px;
    margin: auto;
}

